.wheel {
	width: 30px;
	height: 30px;
	// right: 32px
	position: absolute;
	bottom: 100px;
	right: 20px;
	opacity: 1;
	transition: opacity .3s ease-in-out;
	z-index: 5;
	span {
		position: absolute;
		width: 100%;
		height: 2px;
		opacity: 0;
		transform: scale(.3);
		&:first-child {
			animation: wheel 2s ease-in-out .5s infinite; }
		&:nth-child(2) {
			animation: wheel 2s ease-in-out 1s infinite; }
		&:nth-child(3) {
			animation: wheel 2s ease-in-out infinite; }
		&:before, &:after {
			content: '';
			position: absolute;
			top: 0;
			height: 100%;
			width: 50%;
			background-color: #fff; }
		&::before {
			left: 0;
			transform: skewY(30deg); }
		&::after {
			right: 0;
			width: 50%;
			transform: skewY(-30deg); } } }
