.a-1 {
	background-image: url('../assets/images/about/bg-1.png');
	background-size: cover;
	background-position: left;
	background-repeat: no-repeat;
	&__content {
		.block-title {
			margin-bottom: 30px; }
		.left {
			.desc {
				p {
					font-size: 16px;
