					color: #666666;
					margin-bottom: 30px;
					line-height: 28px;
					strong {
						font-size: 16px;
						font-family: "Montserrat";
						color: rgb(102, 102, 102);
						font-weight: bold; }
					&:last-child {
						margin-bottom: 0; } } } }
		.img {
			padding-top: 25px;
			width: 100%;
			height: 333px;
			position: relative;
			img {
				width: 100%;
				height: 100%;
				object-fit: cover; }
			&::after {
				position: absolute;
				width: 100%;
				height: 25px;
				top: 0;
				left: 25px;
				background-color: rgb(26, 119, 185);
				content: ''; }
			&::before {
				position: absolute;
				width: 25px;
				height: 92.5%;
				top: 0;
				right: -25px;
				background-color: rgb(26, 119, 185);
				content: ''; } }
		.a-1__content {
			.left {
				.desc {
					p {
						margin-bottom: 15px;
						&:last-child {
							margin-bottom: 0; } } } }
			.img {
				padding-top: 15px;
				&::after,
				&::before {
					display: none; } } } }
	@media screen and ( max-width: 575px) {
		#sidebar-wrapper {
			top: 40%; } } }
