			font-size: 16px;
			font-family: "Montserrat";
			color: rgb(51, 51, 51); } }
	.history-slide {
		position: relative;
		.swiper-slide {
			figure {
				cursor: pointer;
				.img {
					width: 100%;
					height: 212px;
					margin-bottom: 30px;
					img {
						width: 100%;
						height: 100%;
						object-fit: cover;
						filter: grayscale(100%);
						transition: all .3s ease-in-out; } }
				figcaption {
					.content {
						overflow: hidden;
						text-overflow: ellipsis;
						display: -webkit-box;
						-webkit-line-clamp: 3;
